import { createApp } from 'vue';
import App from './App.vue';

// 重置浏览器默认 css
import 'reset-css';
// animate.style
import 'animate.css';

// 全局引入 sass 基础样式
import '@/styles/index.scss';

// 创建 VueApp 实例
const app = createApp(App);

// pinia 类似 vuex localStorage
import { createPinia } from 'pinia';
const pinia = createPinia();
app.use(pinia);

// vue 页面路由
import router from '@/router/index';
app.use(router);

// 将 VueApp 实例挂载到节点
app.mount('#app');

// import 语句会自动提升
