/* 防抖函数 */
function debounceFn(func, delay) {
  let timeoutId;

  return function (...args) {
    clearTimeout(timeoutId);

    timeoutId = setTimeout(() => {
      func.apply(this, args);
    }, delay);
  };
}

/* 节流函数 */
function throttleFn(func, delay) {
  let timeoutId;
  let lastExecTime = 0;

  return function (...args) {
    const currentTimestamp = Date.now();
    const elapsedTime = currentTimestamp - lastExecTime;

    if (elapsedTime >= delay) {
      func.apply(this, args);
      lastExecTime = currentTimestamp;
    } else {
      clearTimeout(timeoutId);

      timeoutId = setTimeout(() => {
        func.apply(this, args);
        lastExecTime = Date.now();
      }, delay - elapsedTime);
    }
  };
}

export { debounceFn, throttleFn };
