<template>
  <header>
    <div class="logo">
      <img
        class="animate__animated animate__slow"
        src="~@/assets/svg/terminal.svg"
      />
      <h2 @click="$router.push('/')">Pocket</h2>
    </div>
    <div class="mini-open" :class="{ active: isMiniMenu }">
      <button @click="isMiniMenu = !isMiniMenu">
        <span></span>
        <span></span>
      </button>
    </div>
    <nav
      class="animate__animated animate__faster"
      :class="{
        'mini-menu': isMiniMenu,
        animate__fadeIn: isMiniMenu,
      }"
    >
      <ul>
        <li>博客</li>
        <li>其它项目</li>
        <li>主题切换</li>
        <li>登录</li>
      </ul>
    </nav>
  </header>
</template>

<!-- 博客页首 -->
<script setup>
import { defineOptions, ref } from 'vue';

defineOptions({
  title: 'PocketHeader',
});

const isMiniMenu = ref(false);
</script>

<style lang="scss" scoped>
header {
  display: flex;
  padding: 20px 0;
  justify-content: space-between;
  align-items: center;
  user-select: none;
  background-color: var(--background);

  .logo {
    display: flex;
    align-items: center;

    img {
      display: block;
      width: 50px;
      &:hover {
        animation-name: flash;
      }
    }
    h2 {
      margin: 0 10px;
      cursor: pointer;
    }
  }
  nav {
    --animate-delay: 0s;
    --animate-duration: 0.3s;

    &.mini-menu {
      display: block;
    }

    ul {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-weight: bold;

      li {
        padding: 0 10px;
      }
    }
  }

  .mini-open {
    display: none;

    button {
      width: 20px;
      height: 10px;
      display: flex;
      flex-flow: column wrap;
      justify-content: space-between;
    }

    span {
      display: block;
      height: 2px;
      width: 100%;
      background-color: var(--text);
      transition: transform 0.3s ease;
    }
    &.active span:first-child {
      transform: translateY(4px) rotate(45deg);
    }
    &.active span:last-child {
      transform: translateY(-4px) rotate(-45deg);
    }
  }
}

@media screen and (max-width: $screen-s) {
  header {
    position: fixed;
    top: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
  }
  nav {
    display: none;
    z-index: 1;
    position: fixed;
    top: 50px;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    overflow: hidden;
    background-color: var(--background);
  }
  header > nav > ul {
    flex-flow: column wrap;
    justify-content: stretch;
  }
  div.mini-open {
    display: block;
  }
}
</style>
