<template>
  <!-- 
  <button @click="getInfo">重新获取</button> -->
  <h3>width: {{ deviceInfo[3].value }}</h3>
  <h3>height: {{ deviceInfo[4].value }}</h3>
  <ul>
    <li v-for="(info, index) in deviceInfo" :key="index">
      {{ info.name }}{{ info.value }}
    </li>
  </ul>
</template>

<script setup>
import { defineOptions, ref, onBeforeUnmount } from 'vue';
import {
  getDevicePixelRatio,
  getBrowserLanguage,
  getUserAgent,
  getScreenSize,
  getHtmlSize,
} from '@/utils/device/device-info.js';
import { debounceFn } from '@/utils/utils.js';

defineOptions({
  title: 'DeviceInfo',
});

const deviceInfo = ref([]);

function getInfo() {
  deviceInfo.value = [];
  const screen = getScreenSize();
  const html = getHtmlSize();
  deviceInfo.value.push({
    name: '设备像素比：',
    value: getDevicePixelRatio(),
  });
  deviceInfo.value.push({
    name: '屏幕宽度：',
    value: screen.width + 'px',
  });
  deviceInfo.value.push({
    name: '屏幕高度：',
    value: screen.height + 'px',
  });
  deviceInfo.value.push({
    name: '网页宽度：',
    value: html.width + 'px',
  });
  deviceInfo.value.push({
    name: '网页高度：',
    value: html.height + 'px',
  });
  deviceInfo.value.push({
    name: '浏览器语言：',
    value: getBrowserLanguage(),
  });
  deviceInfo.value.push({
    name: 'UserAgent：',
    value: getUserAgent(),
  });
}
getInfo();

const fn = debounceFn(getInfo, 500);

window.addEventListener('resize', fn);

onBeforeUnmount(() => {
  window.removeEventListener('resize', fn);
});
</script>

<style lang="scss" scoped>
ul {
  border: 1px solid #000;
}
li + li {
  border-top: 1px solid #000;
}
</style>
